.name-logo{
    display: flex; align-items: center;  width: 100%; 
}
.name-logo img{
    width:100%; height: 100%; 
    filter: contrast(150%);
}

@media (max-width:1300px) {
    .name-logo img{
width: 150%;
    }
}
@media (max-width:1100px) {
    .name-logo img{
width: 175%;
    }
}


@media (max-width:750px) {
    .name-logo img{
width: 275%;
    }
}


@media (max-width:650px) {
    .name-logo img{
width: 300%;
    }
}